export const feedSearch = (value) => dispatch => {
    console.log("Performing feed search with value", value)
    searchFeed(value).then( res => {
        if (res) {
            console.log("Got res",res);
            dispatch({
                type: FEEDSEARCH,
                payload: {
                    query: value,
                    content: res
                }
            })
        }
    })
}
const initialState = {contents: ['Mirististica', 'Vet'], value: '', works: []};

export default function reducer(state = initialState, action) {
  switch(action.type) {
    case SEARCH: {
      const {value} = action;
      const works = state.contents.filter((val) => val.includes(value));
      return {...state, value, works};
    }
    case FEEDSEARCH: {
        return action.payload || null;
    }
    default:
      return state;
  }
}